.ProjectBox{
    $bgcolor:#f9f5ea !global;
    $lightblue:#d7edf2 !global;
    $blue:#88a4ca !global;
    $darkblue:#5c7eac !global;
    $pink:#eb6366 !global;
    $fontcolor:#291c16 !global;
    $aroundpadding: 12px 24px !global;
    $sidepadding: 12px !global;
    $title:24px !global;
    $font:16px !global;

    width:80%;
    margin:0 auto;
    margin-bottom:50px;
    .pImg{
        width:50%;
        margin: auto;
        img{
            width:100%;
            height: 100%;
            overflow:hidden;
        }
    }
    .pInfo{
        width:50%;
        padding:$aroundpadding
    }
    a{
        color:$fontcolor;
    }

    .pTitle{
        font-size: $title;
        
    }

    .sub{
        padding:20px;
    }
}